        <el-select
          class="top_sel"
          size="mini"
          v-model="customerId"
          placeholder="客户"
        >
          <el-option
            v-for="item in customerlist"
            :key="item.value1"
            :label="item.label"
            :value="item.value1"
          >
          </el-option>
        </el-select>
        <el-select
          class="top_sel"
          size="mini"
          v-model="plan_statusId"
          placeholder="计划状态"
        >
          <el-option
            v-for="item in plan_statuslist"
            :key="item.value1"
            :label="item.label"
            :value="item.value1"
          >
          </el-option>
        </el-select>
        <el-date-picker
          v-model="planned_date"
          class="eldate"
          size="mini"
          style="width: 200px"
          type="daterange"
          range-separator="至"
          start-placeholder="计划日期"
          end-placeholder="计划日期"
        >
        </el-date-picker>
        <el-button
          class="search_btn"
          type="primary"
          size="mini"
          @click="search()"
          >查 询</el-button
