        <el-table-column prop="Scheduling" label="排期" width="">
        </el-table-column>
        <el-table-column prop="service_plan_cost" label="服务计划费用" width="">
        </el-table-column>
        <el-table-column prop="set_service_date" label="设定服务日期" width="">
        </el-table-column>
        <el-table-column prop="planner" label="计划人" width="">
        </el-table-column>
        <el-table-column prop="planned_date" label="计划日期" width="">
        </el-table-column>
        <el-table-column prop="plan_status" label="计划状态" width="">
        </el-table-column>
        <el-table-column label="操作" width="175">
