    const onSubmit = () => {
      proxy.$refs.servicePlanDialog.setshow();
    };
    const handleEdit = (res) => {
      console.log(res);
      proxy.$refs.servicePlanDialog.setedit(res);
    };
    const handleDel = () => {
      console.log();
    };
    const search = () => {
      console.log();
    };
    const resetForm = () => {
      console.log();
    };
    const change = () => {
      console.log();
    };

    return {
      ...toRefs(state),
      onSubmit,
      handleEdit,
      handleDel,
      search,
      resetForm,
      change,
    };
  },
};
</script>

<style scoped>
.top_box {
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
  box-sizing: border-box;
  margin-top: 10px;
}
.top_sel {
  margin-left: 10px;
}
.search_btn {
  margin-left: 10px;
}

.main_box {
  margin-top: 10px;
  padding: 0 10px;
  box-sizing: border-box;
}
</style>
<style>
.eldate {
  margin-left: 10px;
}
</style>
