      ],
    });
    const { proxy } = getCurrentInstance();
    const setshow = () => {
      proxy.dialogVisible = true;
    };
    const setedit = () => {
      proxy.dialogVisible = true;
    };
    // const reset = () => {
    //   proxy.tableData.push({
    //     index: proxy.tableData.length,
