    //     service_type: "",
    //     service_cost: "",
    //   });
    // };
    const handleEdit = () => {};
    const handleDel = () => {};
    const save = () => {
      proxy.dialogVisible = false;
    };
    const saveandfuwu = () => {
      proxy.dialogVisible = false;
       proxy.$router.push("/serviceInformation");
    }
    const odd = () => {
      proxy.dialogVisible = false;
    };

    return {
      setshow,
      setedit,
      ...toRefs(state),
      handleEdit,
      handleDel,
      odd,
      save,
      saveandfuwu
    };
